.m-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: initial;
  background: rgba(0, 0, 0, 0.6);
}
.m-modal .main {
  background: #FFF;
  border-radius: 0.8rem 0.8rem 0.8rem 0.8rem;
  width: 48rem;
  height: 40rem;
  line-height: initial;
  position: absolute;
  top: 50%;
  right: auto;
  bottom: auto;
  left: 50%;
  z-index: initial;
  margin: -20rem auto auto -24rem;
}
.m-modal .main .title {
  border-bottom: 0.1rem solid #e5e5e5;
  padding: 0 2rem 0 2rem;
  height: 5rem;
  line-height: 5rem;
}
.m-modal .main .content {
  padding: 1.2rem 2rem 1.2rem 2rem;
  height: 30rem;
  line-height: initial;
}
.m-modal .main .toolbar {
  border-top: 0.1rem solid #e5e5e5;
  padding: 0 2rem 0 2rem;
  height: 5rem;
  line-height: 5rem;
  text-align: right;
}
.m-modal .main .toolbar span {
  padding: 0rem 1.5rem 0rem 1.5rem;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
  margin: auto auto auto 1rem;
  height: 3.2rem;
  line-height: 3.2rem;
  color: #FFF;
  display: inline-block;
}
.m-modal .main .toolbar span:last-child {
  background: #FF3858;
}
.m-modal .main .toolbar span:nth-last-child(2) {
  background: #00A0E8;
}



