/** 变量定义 **/
.m-aside-menu {
  cursor: pointer;
  border-right: 0.1rem solid #e5e5e5;
  width: 18rem;
  background: #fff;
  overflow-x: hidden;
  overflow-y: scroll;
  position: fixed;
  top: 6rem;
  right: auto;
  bottom: 0;
  left: 0;
  z-index: initial;
}
.m-aside-menu .group-name {
  border-bottom: 0.1rem solid #e5e5e5;
  padding: 0 1.2rem 0 2rem;
  height: 4.4rem;
  line-height: 4.4rem;
  text-align: right;
}
.m-aside-menu .group-name span {
  float: left;
}
.m-aside-menu .group-items {
  position: relative;
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
  z-index: initial;
  overflow: hidden;
  transition: height .2s linear;
}
.m-aside-menu .item {
  border-bottom: 0.1rem solid #e5e5e5;
  background: #f6f6f6;
  height: 4rem;
  line-height: 4rem;
  padding: 0 2rem 0 4rem;
}



