.m-datalist .search-content .button {
  display: inline-block;
  margin: 1.0rem 4.0rem 0rem 0rem;
  background: #3385ff;
  padding: 1.0rem 2.0rem;
  font-size: 1.6rem;
  color: #fff;
}
/** table数据样式设计 **/
.m-datalist .table-content {
  margin: 1rem 0rem 1rem 0rem;
}
.m-datalist .table-content .t-title {
  background: #FFF;
  border-left: 0.1rem solid #e5e5e5;
  border-top: 0.1rem solid #e5e5e5;
  border-right: 0.1rem solid #e5e5e5;
  padding: 0 1rem 0 1rem;
  height: 4rem;
  line-height: 4rem;
  border-radius: 0.4rem 0.4rem 0 0;
}
.m-datalist .table-content .t-operation span {
  color: #fff;
  cursor: pointer;
  display: inline-block;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
  padding: 0 1.2rem 0 1.2rem;
  margin: 0 0.8rem 0 0;
  height: 2.1rem;
  line-height: 2.1rem;
}
.m-datalist .table-content .t-operation span.edit {
  background: #00A0E8;
}
.m-datalist .table-content .t-operation span.delete {
  background: #FF3858;
}
.m-datalist .table-content table {
  width: 100%;
}
.m-datalist .table-content table td,
.m-datalist .table-content table th {
  border: 0.1rem solid #e5e5e5;
  background: #FFF;
  padding: 0 1rem 0 1rem;
  height: 3rem;
  line-height: 3rem;
  text-align: left;
  cursor: pointer;
}
.m-datalist .table-content table th {
  height: 3.6rem;
  line-height: 3.6rem;
}
.m-datalist .table-content table tbody tr:nth-child(2n) td {
  background: #f3f7f6;
}
.m-datalist .table-content table tbody tr:hover td {
  background: #ecf7f4;
}
.m-datalist .table-content .t-footer {
  background: #FFF;
  border-left: 0.1rem solid #e5e5e5;
  border-right: 0.1rem solid #e5e5e5;
  border-bottom: 0.1rem solid #e5e5e5;
  height: 3.6rem;
  line-height: 3.6rem;
  border-radius: 0 0 0.4rem 0.4rem;
}
.m-datalist .table-content .t-footer ul {
  float: right;
}
.m-datalist .table-content .t-footer ul li {
  float: left;
  width: 3.5rem;
  height: 3.5rem;
  line-height: 3.5rem;
  border-right: 0.1rem solid #e5e5e5;
  cursor: pointer;
  text-align: center;
}
.m-datalist .table-content .t-footer ul li:first-child {
  border-left: 0.1rem solid #e5e5e5;
}
.m-datalist .table-content .t-footer ul li:last-child {
  border-right: 0rem solid #e5e5e5;
}
.m-datalist .table-content .t-footer ul li.selected {
  background: #ecf7f4;
}



