.m-form .item {
  position: relative;
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
  z-index: initial;
  height: 3.6rem;
  line-height: 3.6rem;
  padding: 0 0.8rem 0 5rem;
}
.m-form .item label {
  position: absolute;
  top: 0;
  right: auto;
  bottom: 0;
  left: 0;
  z-index: initial;
}
.m-form .item input {
  border: 0.1rem solid #e5e5e5;
  border-radius: 0.4rem 0.4rem 0.4rem 0.4rem;
  height: 3rem;
  line-height: 3rem;
  padding: 0 1rem 0 1rem;
}



